import {
  CheckoutContainer,
  CheckoutHeader,
  CheckoutHeaderBlock,
  CheckoutTotal,
} from "./checkout.styles";
import CheckOutItem from "../../components/checkout-item/checkout-item.component";
import { CartContext } from "../../contexts/cart.context";
import { useContext, useState } from "react";
import { useNavigate } from "react-router-dom";
import Button from "../../components/button/button.component";
import Toast from "../../components/additional-components/toast/toast.component";

const CheckOut = () => {
  const { cartItems, cartTotal } = useContext(CartContext);
  const navigate = useNavigate();
  const [showAlert, setShowAlert] = useState(false);

  const onCheckOut = () => {
    if (cartTotal === 0) {
      setShowAlert(true);
      setTimeout(() => {
        setShowAlert(false);
      }, 5000);
      return;
    }
    navigate("/payment");
  };

  return (
    <CheckoutContainer>
      <CheckoutHeader>
        <CheckoutHeaderBlock>محصول</CheckoutHeaderBlock>
        <CheckoutHeaderBlock>شرح</CheckoutHeaderBlock>
        <CheckoutHeaderBlock>تعداد</CheckoutHeaderBlock>
        <CheckoutHeaderBlock>قیمت</CheckoutHeaderBlock>
        <CheckoutHeaderBlock>حذف</CheckoutHeaderBlock>
      </CheckoutHeader>
      {cartItems.map((item) => (
        <CheckOutItem key={item.id} cartItem={item} />
      ))}
      <CheckoutTotal>جمع کل:<span>{cartTotal}</span><span>تومان</span></CheckoutTotal>
      <Button onClick={onCheckOut}>پرداخت</Button>
      {showAlert && (
        <Toast
          color="red"
          message={"سبد خالی است! لطفا موارد را به سبد خرید اضافه کنید"}
        />
      )}
    </CheckoutContainer>
  );
};

export default CheckOut;
